<template>
    <div class="flex-page" ref="flexPage">
        <van-nav-bar class="flex-full" ref="flexFull">
            <div slot="left" class="leftBar">
                <div class="backIcon" @click="goToBack">
                    <img src="@/assets/images/common/nav_return_black.png" alt="">
                </div>
                <div class="searchInput" @click="goToGoodsSearch()">{{searchKeyword.length > 0 ? searchKeyword : defaultWord}}</div>
                <img class="voiceIcon" src="@/assets/images/mall/mall_icon_voice.png" alt="" v-show="isShowVoiceBtn" @click="goToSpeech">
                <dialog-bar v-model="sendVal" type="danger" v-bind:title="recognizeResult" content="您可以说：“花生油”<br />“我想要买花生油”<br />..." v-on:cancel="clickCancel()" @danger="clickDanger()" @confirm="clickConfirm()" dangerText="Delete" ></dialog-bar>
            </div>
            <div slot="right" class="rightBar" @click="goToShoppingCart()">
                <img ref="cartIcon" src="@/assets/images/newSlicing/icon_shop_cut.png" alt="">
                <div class="goodsNum" :class="skuTypeNum>=100?'goodsNumMax':''" v-show="skuTypeNum!=0">
                    {{skuTypeNum<=99?skuTypeNum:'99+'}}
                </div>
            </div>
        </van-nav-bar>
        <div class="post-wrapper" ref="contentScroll" :style="{height:clientHeight+'px'}">
            <div class="tabBarList" ref="tabBarHeight">
                <div class="shadowBoxL">
                    <img src="@/assets/images/mall/gradient_l.png" alt="">
                </div>
                <div class="scrollX" ref="tabBarScroll">
                    <div class="btnItem" ref="tabBarItem" :class="goodsCateIndex==index?'btnItemAct':''" @click="changeMoreList(index)"
                         v-for="(goodsCateItem,index) in goodsCateList" :key="'goodsCateItem'+index">
                        <div class="benItemImg">
                            <img :src="goodsCateItem.catePic" alt="">
                        </div>
                        <div class="btnItemNameBox">
                            <div class="btnItemName">{{goodsCateItem.cateName}}</div>
                        </div>
                    </div>
                </div>
                <div class="shadowBoxR">
                    <img src="@/assets/images/mall/gradient_r.png" alt="">
                </div>
            </div>
            <div class="goodsBlankBox" v-show="secondCateList.length==0&&secondCateFirstHttp">
                <img src="@/assets/images/default/mall_classification_default.png" alt="">
                <div>商品正在努力丰富中
                    <br>
                    敬请期待...</div>
            </div>

            <div class="classifyAndGoodsList" :style="{height:tabBarHeight+'px'}" v-show="secondCateList.length!=0">

                <div class="classifyList" ref="classifyScroll">
                    <div class="classifyScroll" >
                        <div class="classifyItem" :class="secondCateIndex==index?'classifyItemAct':''" @click="changeSecondMoreList(index)"
                             v-for="(secondCateItem,index) in secondCateList" :key="'secondCateItem'+index">
                            <div class="classifyIcon"></div>
                            <div class="classifyTxt">{{secondCateItem.cateName}}</div>
                        </div>
                        <div class="classifyItem">
                            <div class="classifyIcon"></div>
                        </div>
                    </div>
                </div>
                <div class="goodsListBox" ref="goodsListBox" @touchstart="handleTouchStart"
                     @touchend="handleTouchEnd"
                     @touchmove="handleTouchMove">
                    <van-list
                            class="goodsVantList"
                            ref="goodsBantList"
                            v-model="loading"
                            :finished="finished"
                            @load="onLoad"
                            style="width:100%;"
                            :offset="offsetHeight"
                    >
                        <div class="goodsListTitle" ref="goodsListTitle">
                            <div class="salePriceSort">
                                <div class="saleSort" @click="clickSaleBtn()">
                                    <div>销量</div>
                                    <div class="redLineBox" v-show="sortMethod==3"><img
                                            src="@/assets/images/mall/redLine.png" alt=""></div>
                                </div>
                                <div class="priceSortBox" @click="priceSort()">
                                    <div class="priceSort">
                                        <div class="priceSortTxt">价格</div>
                                        <img :src="sortMethod==''||sortMethod==3?icon.filter:(sortMethod==1?icon.filterUp:icon.filterDown)"
                                             alt="">
                                    </div>
                                    <div class="redLineBox" v-show="sortMethod==1||sortMethod==2"><img
                                            src="@/assets/images/mall/redLine.png" alt=""></div>
                                </div>
                            </div>
                        </div>
                        <div class="goodsList" ref="goodsList" :style="{minHeight:goodsListHeight+'px'}">
                            <div class="goodsBox" :style="goodsIndex == goodsListData.length-1?{marginBottom:0}:{}" v-for="(goodsItem,goodsIndex) in goodsListData"
                                 :key="'goodsIndex'+goodsIndex">
                                <div class="goodsItem" @click="goToProduct(goodsItem)">
                                    <div class="goodsImg" >
                                        <!--                                    <img v-lazy="goodsItem.spuPicUrl" alt="" :key="goodsItem.spuPicUrl">-->
                                        <img :src="goodsItem.spuPicUrl" alt="" :onerror="defaultImg">
                                        <div class="showActivitySpu" v-show="goodsItem.activitySpuId">秒杀</div>
                                        <div class="promotionBox" v-show="goodsItem.actType!=null" :class="goodsItem.actType==0||goodsItem.actType==1?'greenPromotionBox':'redPromotionBox'">{{goodsItem.actTag}}</div>
                                        <div class="outSellBox" v-show="goodsItem.storeNumber==0&&!goodsItem.activitySpuId">补货中</div>
                                        <div class="jinshengNum" v-show="goodsItem.storeNumber<=goodsItem.threshold&&goodsItem.storeNumber!=0&&!goodsItem.activitySpuId">仅剩{{goodsItem.storeNumber}}件</div>
                                    </div>
                                    <div class="goodsInfo">
                                        <div class="productInfo">
                                            <div class="goodsName" style="line-height: 24px;">{{goodsItem.spuName}}</div>
                                            <div class="goodsDesc">{{goodsItem.spuSalePoint}}</div>
                                            <div class="goodsSale">月售{{goodsItem.showSale | threshold}}</div>
                                            <div class="labelList">
                                                <div class="labelItem">{{goodsItem.shopName}}</div>
                                                <div class="labelItem" v-show="goodsItem.couponFlag==1">券</div>
                                            </div>
                                        </div>
                                        <div class="goodsPriceBx">
                                            <div class="goodsPriceInfo">
                                                <div class="goodsPrice">
                                                    <div class="goodsPriceIcon">￥</div>
                                                    <div class="goodsPriceNum">{{goodsItem.skuPrice|priceFilters}}</div>
                                                </div>
                                                <div class="goodsLinePrice" v-if="goodsItem.priceLine">
                                                    ￥{{goodsItem.priceLine|priceFilters}}
                                                </div>
                                            </div>
                                            <div class="addMinusBtn">
                                                <div class="clickBox" @click.stop="addGoods(goodsItem,goodsIndex)"></div>
                                                <div class="cartNumBox" v-show="goodsItem.cartCount!=0" :class="goodsItem.cartCount>99?'cartNumBigBox':''" @click.stop="addGoods(goodsItem,goodsIndex)">
                                                    <div class="cartNum">{{goodsItem.cartCount}}</div>
                                                </div>
                                                <img class="addBtn" ref="addBtn" src="@/assets/images/mall/mall_icon_add.png" alt="" @click.stop="addGoods(goodsItem,goodsIndex)">
                                                <!-- <img class="addBtn" ref="addBtn" v-show="goodsItem.storeNumber!=0" src="@/assets/images/mall/mall_icon_add.png" alt="" @click.stop="addGoods(goodsItem,goodsIndex)"> -->
                                                <!-- <img class="addBtn" v-show="goodsItem.storeNumber==0" src="@/assets/images/mall/mall_icon_add_gray.png" alt="" @click.stop="addGoods(goodsItem,goodsIndex)"> -->
                                            </div>
                                        </div>
                                    </div>
                                </div>
                                <div class="lineBox"  v-show="goodsIndex != goodsListData.length-1"></div>
                            </div>
                            <div class="goodsBlankBox" v-if="goodsListData.length==0&&goodsFirstHttp">
                                <img src="@/assets/images/default/mall_classification_default.png" alt="">
                                <div>商品正在努力丰富中
                                    <br>
                                    敬请期待...</div>
                            </div>
                        </div>
                        <div class="finishedBox" ref="finishedBox" :style="{paddingBottom:this.$stbHeight+'px'}" v-show="finished&&goodsListData.length!=0">
                            <img src="@/assets/images/mall/mall_logo.png" alt="">
                        </div>
                    </van-list>

                    <!-- <div style="height:100px" v-show="finished">上拉跳转下一页</div> -->

                </div>
            </div>
        </div>
        <transition @before-enter="beforeEnter" @enter="enter" @after-enter="afterEnter">
            <img class="circle" v-show="isShowCircle" src="@/assets/images/mall/mall_icon_add.png" alt="">
        </transition>
    </div>
</template>


<script>
    import {moreClassify} from '../../../page/superiorProduct/moreClassify'
    export default {
        name: 'productPageThemeL',
        mixins:[moreClassify]
    }
</script>

<style scoped lang="scss">
    .promotionBox{
        position: absolute;
        top: 0;
        min-width: 60px;
        height: 16px;
        border-radius: 4px 0px 8px 0px;
        font-family: PingFangSC-Regular;
        font-size: 10px;
        color: #FFFFFF;
        letter-spacing: 0;
        text-align: center;
        line-height: 16px;
        padding: 0 5px;
        box-sizing: border-box;
        white-space: nowrap;
    }
    .redPromotionBox{
        background-image: linear-gradient(143deg, #FFA78E 0%, #FA452D 82%);
    }
    .greenPromotionBox{
        background-image: linear-gradient(143deg, #D8FF8A 0%, #66B22B 82%);
    }
 .showActivitySpu{
        position: absolute;
        top: 0;
        width: 40px;
        height: 16px;
        background: url("../../../assets/images/mall/Spike_labelcut.png") no-repeat;
        -webkit-background-size: cover;
        background-size: cover;
        text-align: center;
        line-height: 16px;
        font-family: PingFangSC-Regular;
        font-size: 10px;
        color: #FFFFFF;
        letter-spacing: 0;
    }
    .circle{
        width: 22px;
        height: 22px;
        position: fixed;
        z-index: 100;
        transition: all .3s ease;
    }
    /*导航栏*/
    /deep/.flex-full{
        height: 44PX!important;
        display: flex;
        align-items: center;
        .van-nav-bar__left,.van-nav-bar__right{
            height: 44PX!important;
        }
        .van-nav-bar__right{
            right: 5px;
            width: 44px!important;
            height: 44px!important;
            /*background: green;*/
            bottom: auto;
        }
    }
    .leftBar {
        height: 44PX;
        position: relative;

        .backIcon {
            width: 32px;
            position: absolute;
            top: 0;
            left: -9px;
            z-index: 10;

            img {
                margin-top: 4px;
                width: 32px;
            }
        }

        .voiceIcon {
            width: 20px;
            height: 20px;
            position: absolute;
            top: 12px;
            right: 8px;
        }
    }
    .searchInput {
        width: 275px;
        height: 34PX;
        background: #F7F7F7;
        border-radius: 2px;
        border: 0;
        padding-left: 32px;
        padding-right: 32px;
        font-family: PingFangSC-Regular;
        font-size: 13px;
        color: #9A9A9A;
        text-align: left;
        letter-spacing: -0.04px;
        line-height: 34PX;
        background-image: url(../../../assets/images/mall/search_black.png);
        background-repeat: no-repeat;
        -webkit-background-size: 14px 14px;
        background-size: 14px 14px;
        background-position: 10px 9px;
        position: relative;
        top: 6PX;
        box-sizing: border-box;
        margin-left: 37px;
    }
    .rightBar {
        width: 44px;
        height: 44px;
        position: relative;

        img {
            width: 32px;
            height: 32px;
            position: absolute;
            top: 50%; /*偏移*/
            left: 0;
            transform: translateY(-50%);
        }

        .goodsNum {
            width: max-content;
            min-width: 19px;
            height: 19px;
            text-align: center;
            border-radius: 9.5px;
            letter-spacing: -0.02px;
            background: #FC0238;
            position: absolute;
            top: 3px;
            left: 16px;
            font-size:  11px;
            color: #FFFFFF;
            box-sizing: border-box;
            display:flex;
            justify-content:center;
            align-items:center;
            line-height: 19px;
        }
        .goodsNumMax{
            padding: 0 3px;
            min-width: 30px;
        }
    }

    .tabBarList{
        height: 110px;
        padding: 15px 0;
        background-image: linear-gradient(-90deg, rgba(249,249,249,0.00) 0%, #F9F9F9 31%);
        box-sizing: border-box;
        position: relative;
        .scrollX{
            display: flex;
            overflow: auto;
            .btnItem {
                /*width: 55px;*/
                /*margin-right: 20px;*/
                .benItemImg {
                    width: 55px;
                    height: 55px;
                    border-radius: 20px;
                    overflow: hidden;
                    box-sizing: border-box;
                    border: 1px solid #f9f9f9;
                    img {
                        width: 55px;
                        height: 55px;
                        object-fit: cover;
                    }
                }
                .btnItemNameBox{
                    width: 75px;
                }
                .btnItemName {
                    width: 65px;
                    margin-left: -5px;
                    font-family: PingFangSC-Regular;
                    font-size: 14px;
                    color: #333333;
                    letter-spacing: -0.04px;
                    text-align: center;
                    margin-top: 4px;
                    overflow: hidden;
                    white-space:nowrap;
                }

            }
            .btnItem:first-child{
                padding-left: 30px;
            }
            .btnItem:last-child{
                padding-right: 30px;
            }
            .btnItemAct{
                .benItemImg{
                    border: 1px solid #17B7BD;
                }
                .btnItemName{
                    font-weight: 600;
                    color: #17B7BD;
                }

            }
        }
        .scrollX::-webkit-scrollbar {
            display: none;
        }
        .shadowBoxL{
            width: 30px;
            height: 105px;
            position: absolute;
            top: 0px;
            left: 0px;
            img{
                width: 30px;
                height: 95px;
            }
        }
        .shadowBoxR{
            width: 30px;
            height: 105px;
            position: absolute;
            top: 0px;
            right: 0px;
            img{
                width: 30px;
                height: 95px;
            }
        }
    }
    /*background-image: linear-gradient(-90deg, rgba(249,249,249,0.00) 0%, #F9F9F9 31%);*/

    .classifyAndGoodsList{
        display: flex;
        .classifyList{
            width: 76px;
            background: #F7F7F7;
            overflow: auto;
            .classifyItem{
                display: flex;
                .classifyIcon{
                    width: 5px;
                    height: 60px;
                }
                .classifyTxt{
                    font-family: PingFangSC-Regular;
                    font-size: 15px;
                    color: #333333;
                    letter-spacing: -0.04px;
                    line-height: 60px;
                    text-align: center;
                    width: 100%;
                    overflow: hidden;
                    text-overflow:ellipsis;
                    white-space: nowrap;
                }
            }
            .classifyItemAct{
                background: #FFFFFF;
                .classifyIcon{
                    background: #17B7BD;
                }
                .classifyTxt{
                    color: #17B7BD;
                    font-weight: 600;
                }
            }
        }
        .classifyList::-webkit-scrollbar {
            display: none;
        }
    }

    .goodsListBox{
        width: 299px;
        overflow-y: auto;
        overflow-x: hidden;
        .goodsListTitle{
            display: flex;
            align-items: center;
            justify-content:flex-end;
            height: 36px;
            border-bottom: 1px solid #F0F0F0;
            .salePriceSort {
                display: flex;
                align-items: center;
                padding-right: 20px;

                .saleSort {
                    font-family: PingFangSC-Regular;
                    font-size: 14px;
                    color: #4A4A4A;
                    letter-spacing: -0.03px;
                    line-height: 18px;
                    margin-right: 20px;
                    position: relative;

                    .redLineBox {
                        width: 20px;
                        height: 3px;
                        display: flex;
                        align-items: center;
                        position: absolute;
                        bottom: -3px;
                        left: 2px;

                        img {
                            width: 20px;
                        }
                    }
                }

                .priceSortBox {
                    position: relative;

                    .priceSort {
                        display: flex;
                        align-items: center;

                        .priceSortTxt {
                            font-family: PingFangSC-Regular;
                            font-size: 14px;
                            color: #4A4A4A;
                            letter-spacing: -0.03px;
                            line-height: 18px;
                        }

                        img {
                            width: 9px;
                            margin-left: 2px;
                        }
                    }

                    .redLineBox {
                        width: 20px;
                        height: 3px;
                        /*margin:0 auto;*/
                        display: flex;
                        align-items: center;
                        position: absolute;
                        bottom: -3px;
                        left: 2px;

                        img {
                            width: 20px;
                        }
                    }
                }
            }
        }
        .goodsList{
            .goodsBox {
                margin-bottom: 14px;
                .goodsItem{
                    display: flex;
                    padding: 14px 15px;
                    .goodsImg {
                        width: 120px;
                        height: 120px;
                        overflow: hidden;
                        position: relative;
                        border-radius: 4px;

                        img {
                            width: 100%;
                        }
                        .jinshengNum{
                            width: 100%;
                            height: 25px;
                            text-align: center;
                            position: absolute;
                            bottom: 0;
                            background: rgba(0, 0, 0, 0.50);
                            font-size: 12px;
                            color: #BCBCBC;
                            letter-spacing: -0.04px;
                            line-height: 25px;
                        }
                        .outSellBox{
                            width: 70px;
                            height: 70px;
                            text-align: center;
                            line-height: 70px;
                            font-size: 12px;
                            color: #FFFFFF;
                            letter-spacing: -0.04px;
                            background: rgba(0,0,0,0.50);
                            border-radius: 50%;
                            position: absolute;
                            top: 50%;
                            left: 50%;
                            transform: translate(-50%,-50%);
                        }

                    }
                    .goodsInfo {
                        width: 140px;
                        margin-left: 10px;

                        .productInfo {
                        }

                        .goodsName {
                            font-family: PingFangSC-Medium;
                            font-size: 17px;
                            color: #333333;
                            letter-spacing: -0.06px;
                            overflow: hidden;
                            text-overflow: ellipsis;
                            display:-webkit-box;
                            -webkit-box-orient:vertical;
                            -webkit-line-clamp:2;
                        }

                        .goodsDesc {
                            font-family: PingFangSC-Regular;
                            font-size: 12px;
                            color: #888888;
                            letter-spacing: -0.04px;
                            line-height: 16px;
                            overflow: hidden;
                            white-space: nowrap;
                            text-overflow: ellipsis;
                        }

                        .goodsSale {
                            font-family: PingFangSC-Regular;
                            font-size: 12px;
                            color: #BCBCBC;
                            letter-spacing: -0.04px;
                            line-height: 16px;
                            margin-top: 3px;
                        }
                        .labelList {
                            display: flex;
                            flex-wrap: wrap;
                            margin-top: 5px;

                            .labelItem{
                                max-width: 100px;
                                position: relative;
                                padding: 2PX 6px;
                                margin-right: 0;
                                height: 12PX;
                                line-height: 13PX;
                                font-size: 10px;
                                color: #e4393c;
                                background: #fff;
                                word-break: break-word;
                                font-style: normal;
                                vertical-align: middle;
                                -webkit-tap-highlight-color: rgba(0,0,0,0);
                                outline: 0;
                                overflow: hidden;
                                white-space: nowrap;
                                text-overflow: ellipsis;
                            }
                            .labelItem:before {
                                content: "";
                                display: block;
                                border: 1px solid #ddd;
                                position: absolute;
                                top: 0;
                                bottom: -100%;
                                left: 0;
                                right: -100%;
                                pointer-events: none;
                                border-color: #e4393c;
                                border-radius: 200px;
                                -webkit-transform: scale(.5);
                                -webkit-transform-origin: 0 0;
                            }
                        }

                        .goodsPriceBx {
                            display: flex;
                            align-items: center;
                            justify-content: space-between;
                            margin-top: 9px;
                            .goodsPriceInfo {
                                max-width: 110px;
                                overflow: hidden;
                                display: flex;
                                align-items: center;
                            }

                            .goodsPrice {
                                display: flex;
                                align-items: flex-end;

                                .goodsPriceIcon {
                                    font-family: PingFangSC-Medium;
                                    font-size: 13px;
                                    color: #F43D3D;
                                    letter-spacing: -0.04px;
                                    line-height: 20px;
                                }

                                .goodsPriceNum {
                                    font-family: PingFangSC-Medium;
                                    font-size: 20px;
                                    color: #F43D3D;
                                    letter-spacing: -0.04px;
                                    line-height: 20px;
                                    white-space: nowrap;
                                }
                            }

                            .goodsLinePrice {
                                font-family: PingFangSC-Regular;
                                font-size: 13px;
                                color: #9A9A9A;
                                letter-spacing: -0.03px;
                                text-align: right;
                                line-height: 28px;
                                text-decoration: line-through;
                                margin-left: 2px;
                            }

                            .addMinusBtn {
                                display: flex;
                                position: relative;
                                .clickBox{
                                    width: 55px;
                                    height: 55px;
                                    position: absolute;
                                    top: -17px;
                                    left: -17px;
                                }
                                .cartNumBox{
                                    width: 18px;
                                    height: 18px;
                                    position: absolute;
                                    top: -9px;
                                    right: -9px;
                                    background: #fff;
                                    border-radius: 50%;
                                    .cartNum{
                                        width: 16px;
                                        height: 16px;
                                        margin: 1px auto 0px;
                                        box-sizing: border-box;
                                        border-radius: 50%;
                                        border: 1px solid #17B7BD;
                                        font-family: PingFangSC-Semibold;
                                        font-size: 10px;
                                        color: #17B7BD;
                                        letter-spacing: -0.03px;
                                        text-align: center;
                                        line-height: 16px;
                                    }

                                }
                                .cartNumBigBox{
                                    width: 28px;
                                    height: 18px;
                                    border-radius: 30px;
                                    .cartNum{
                                        width: 26px;
                                        height: 16px;
                                        border-radius: 30px;
                                        margin: 1px auto 0px;
                                        box-sizing: border-box;
                                        border: 1px solid #17B7BD;
                                        font-family: PingFangSC-Semibold;
                                        font-size: 10px;
                                        color: #17B7BD;
                                        letter-spacing: -0.03px;
                                        text-align: center;
                                        line-height: 15px;
                                    }
                                }
                                .MinusBtn {
                                    width: 22px;
                                    height: 22px;
                                }

                                .goodsNum {
                                    width: 30px;
                                    font-size: 16px;
                                    color: #333333;
                                    letter-spacing: -0.06px;
                                    text-align: center;
                                    line-height: 22px;
                                }

                                .addBtn {
                                    width: 22px;
                                    height: 22px;
                                }

                            }
